.App {
  text-align: center;

  .App-logo {
    height: 30vmin;
    margin-bottom: 25px;
    filter: hue-rotate(-135deg) saturate(200%) invert(150%) brightness(50%);
  }

  .App-header {
    background-color: #ededed;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: #2b2b2b;
    padding: 0 5%;
    a{
      text-decoration: none;
      color: #af2336 !important;
    }
    code{
      color: #917c08;
    }
  }

  .App-link {
    color: #af2336;
  }
  
  .App-footer{
    padding: 25px;
  }
}